n\n// forms\n$mmf-height-label: 1.1em !default;\n\n// animation\n$mmf-transition--time: 0.1s;\n$mmf-transition: $mmf-transition--time linear;\n","@import \"../../lib/reset\";\n@import \"../../lib/interface\";\n@import \"../../lib/icon\";\n@import \"../../lib/units\";\n\n\n@mixin checkbox-with-custom-icon() {\n    @include reset-box();\n    @include reset-input();\n\n    &:checked:after {\n        content: \"check_box\";\n    }\n\n    &:after {\n        @include clickable();\n        @include material-icon();\n        content: \"check_box_outline_blank\";\n        transform: translateY(10%); // center\n    }\n}\n\n.mmf-checkbox {\n    @include clickable();\n    @include checkbox-with-custom-icon();\n\n    &[disabled]:after {\n        cursor: default;\n    }\n}\n","@mixin reset-button() {\n    background: none repeat scroll 0 0 transparent;\n    border: medium none;\n    border-radius: 0;\n    border-spacing: 0;\n    outline: 0;\n    user-select: none;\n    &::-moz-focus-inner,\n    &::-moz-focus-inner {\n        border: 0;\n        padding: 0;\n    }\n    &:-moz-focusring {\n        color: transparent;\n        text-shadow: 0 0 0 #000;\n    }\n}\n\n@mixin reset-select() {\n    @include reset-input();\n    &:-moz-focusring {\n        color: transparent;\n        text-shadow: 0 0 0 #000;\n    }\n\n    * {\n        @include reset-input();\n    }\n}\n\n@mixin reset-input() {\n    background-color: inherit;\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    &::-ms-expand { // IE 11\n        display: none;\n    }\n    border-radius: 0;\n    border: medium none;\n    font-size: inherit;\n}\n\n@mixin reset-box() {\n    margin: 0;\n    padding: 0;\n}\n","@mixin material-icon() {\n    font-family: \"Material Icons\";\n    font-weight: normal;\n    font-style: normal;\n    line-height: 1;\n    letter-spacing: normal;\n    text-transform: none;\n    display: inline-block;\n    white-space: nowrap;\n    word-wrap: normal;\n    direction: ltr;\n    -webkit-font-feature-settings: \"liga\";\n    -webkit-font-smoothing: antialiased;\n}\n","@import \"../../lib/units\";\n\n\n.mmf-form--checkbox {\n\n    label {\n        display: inline-flex;\n        flex-direction: row;\n        align-items: center;\n    }\n\n    .order--label-first .mmf-label {\n        padding-right: $mmf-unit;\n    }\n\n    .order--label-last .mmf-label {\n        padding-left: $mmf-unit;\n    }\n}\n","@import \"../../lib/units\";\n\n\nul.mmf-form__errors {\n    list-style: none;\n    padding-left: 0;\n    margin: 0;\n\n    li {\n        margin-top: 0.5 * $mmf-unit;\n    }\n}\n",".mmf-input {\n    box-sizing: border-box;\n    width: 100%;\n}\n",".mmf-form--input {\n    label {\n        display: flex;\n        flex-direction: column;\n    }\n}\n","@import \"../../lib/reset\";\n@import \"../../lib/icon\";\n@import \"../../lib/colors\";\n\n\n.mmf-select__wrapper {\n    position: relative;\n    display: inline-block;\n\n    &:after {\n        content: \"keyboard_arrow_down\";\n        @include material-icon();\n        align-items: center;\n        bottom: 0;\n        display: flex;\n        pointer-events: none; // remove icon to be clickable (select receives input-event)\n        position: absolute;\n        right: 4px;\n        top: 0;\n    }\n\n    &.is-disabled:after {\n        opacity: $mmf-opacity--disabled;\n    }\n}\n\n.mmf-select {\n    // hide select arrow-icon\n    -moz-appearance: window;\n    -webkit-appearance: none;\n\n    // hide touch borders\n    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n\n    // select layout\n    padding: $mmf-unit; // sane base padding, which conflicts with theme-material\n    padding-right: (0.5 * $mmf-block + 4px);\n\n    &:not([disabled]) {\n        cursor: pointer;\n    }\n}\n","@import \"./color-palettes\";\n// material design colors https://www.materialui.co/flatuicolors\n// https://material.io/color/#!/?view.left=0&view.right=1&secondary.color=90CAF9&primary.color=ECEFF1\n\n// control variables: darken, lighten\n$mmf-color-distance: 20%;\n$mmf-color-distance--focus: 10%;\n// control variables: text\n$mmf-opacity--primary: 0.87 !default;\n$mmf-opacity--secondary: 0.54 !default;\n$mmf-opacity--hint: 0.38 !default;\n$mmf-opacity--disabled: $mmf-opacity--hint !default;\n$mmf-opacity--divider: 0.12 !default;\n\n// base text colors\n$mmf-color--dark: #000 !default;\n$mmf-color--light: #fff !default;\n$mmf-color--ok: #2ECC71 !default;\n$mmf-color--error: red(700) !default;\n$mmf-color--warning: #F39C12 !default;\n\n// strange variable, used often as card background and light-alternative...\n// this could also be sort if highest elevation (besides 100% bright: #fff)\n// \"contrast to bright\" (on hover)\n$mmf-color--contrast: #ECEFF1 !default;\n\n// PRIMARY COLOR\n// In Material Design, a primary color refers to a color that appears most frequently in your app\n$mmf-color--primary: green(2) !default;\n\n// SECONDARY COLOR\n// A secondary color refers to a color used to accent key parts of your UI\n$mmf-color--secondary: green(700) !default;\n\n// layers\n// @todo refactor\n$color-elevation: #37474F;\n$color-elevation-0: $color-elevation; // application background\n// $color-elevation-2: #1c313a; // first layer above background (header-bar)\n$color-elevation-4: darken($color-elevation, 0.25 * $mmf-color-distance); // first layer above background (header-bar)\n$color-elevation-8: darken($color-elevation, 0.125 * $mmf-color-distance); // second layer above background (navigation-bar)\n\n// styling - no colors\n$mmf-style--border: solid !default;\n","@import \"../../lib/units\";\n\n\n.mmf-form--select {\n    label {\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n    }\n\n    .order--label-first .mmf-label {\n        padding-right: $mmf-block-padding;\n    }\n\n    .order--label-last .mmf-label {\n        padding-left: $mmf-block-padding;\n    }\n}\n\n\n","@import \"../../lib/reset\";\n@import \"../../lib/interface\";\n@import \"